<template>
  <div class="rank-wrapper" ref="wrapper">
    <scroll
      ref="scroll"
      :dataArr="Array.from(rankList)"
      class="scroll"
      :click="true"
    >
      <ul class="rank-list" @click="songListHandle($event)">
        <li
          v-for="(item, index) in rankList"
          :key="index"
          class="rank-item"
          :data-id="item[1].id"
        >
          <div class="rank-icon" :style="getSongListIcon(item[1])">
            <p>{{getUpdateTime(item[1].updateTime)}}更新</p>
          </div>
          <div class="song-list" v-if="songArr.get(item[1].id)">
            <p>1.{{getSongNameAndSingerName(songArr.get(item[1].id).songList[0])}}</p>
            <p>2.{{getSongNameAndSingerName(songArr.get(item[1].id).songList[1])}}</p>
            <p>3.{{getSongNameAndSingerName(songArr.get(item[1].id).songList[2])}}</p>
          </div>
          <div class="song-load" v-if="!songArr.get(item[1].id)">
            <loading/>
          </div>
        </li>
      </ul>
    </scroll>
    <transition name="rankDetail">
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
import setBottom from 'mixin/scaleDomHeight';
import getRankInfo from 'api/rank/rank';
import { getRecommendSongList } from 'api/song/song';
import Scroll from 'components/common/scroll/Scroll.vue';
import Loading from 'components/common/loading/Loading.vue';
import { createNamespacedHelpers } from 'vuex';

const { mapMutations } = createNamespacedHelpers('rankDetailModule');
export default {
  mixins: [setBottom],
  components: {
    Scroll,
    Loading,
  },
  methods: {
    handlePlayList() {
      if (this.playList.length === 0) {
        this.$refs.wrapper.style.bottom = '0px';
      } else {
        this.$refs.wrapper.style.bottom = `${this.miniPlayerHeight}px`;
      }
      this.$refs.scroll.refresh();
    },
    getUpdateTime(timeStamp) {
      const time = new Date(timeStamp);
      const year = time.getMonth() + 1;
      const day = time.getDate();
      return `${this._fill(year)}月${this._fill(day)}日`;
    },
    getSongListIcon(item) {
      return {
        backgroundImage: `url(${item.icon})`,
      };
    },
    getSongNameAndSingerName(item) {
      const singer = item.singerName.join('/');
      return `${item.name}-${singer}`;
    },
    songListHandle(event) {
      const target = event.target.closest('.rank-item');
      if (!target) {
        return;
      }
      const { id } = target.dataset;
      const { name, icon } = this.rankList.get(+id);
      const { songList, songIdList } = this.songArr.get(+id);
      this.setName(name);
      this.setIcon(icon);
      this.setSongList(songList);
      this.setSongIdList(songIdList);
      this.$router.push({
        name: 'rankDetail',
        params: {
          id,
        },
      });
    },
    _fill(value) {
      if (value >= 10) {
        return value;
      }
      return `0${value}`;
    },
    ...mapMutations([
      'setSongList',
      'setSongIdList',
      'setName',
      'setIcon',
    ]),
  },
  data() {
    return {
      rankList: new Map(),
      songArr: new Map(),
    };
  },
  created() {
    getRankInfo()
      .then((res) => {
        const newList = new Map();
        res.forEach((item) => {
          newList.set(item.id, item);
        });
        this.rankList = newList;
      })
      .then(() => {
        this.rankList.forEach((item) => {
          getRecommendSongList(item.id)
            .then((res) => {
              this.songArr = new Map([...this.songArr].concat([[item.id, res]]));
            });
        });
      })
      .catch((err) => {
        console.log('rank get failed: ', err);
      });
  },
};
</script>
