import request from 'assets/js/network';

async function getRankInfo() {
  try {
    const res = await request({
      url: '/toplist',
    });
    const rankList = res.list.map((item) => ({
      name: item.name,
      id: item.id,
      icon: item.coverImgUrl,
      updateTime: item.updateTime,
    }));
    return rankList;
  } catch (err) {
    return Promise.reject(err);
  }
}
export default getRankInfo;
